@import '../../../styles/mixins.scss';

.title {
    margin-bottom: 32px;
}

.info {
    margin-bottom: 32px;
    @include body-2;

    strong {
        font-weight: 500;
    }
}

.stage {
    margin-bottom: 16px;
    @include body-bold-1;
}

.row {
    display: flex;
    padding-top: 12px;

    &:first-child {
        padding: 0 0 12px;
        border-bottom: 1px solid $neutrals6;
        font-weight: 500;

        @include dark {
            border-color: $neutrals3;
        }

        .col {
            font-size: 30px;
        }
    }
}

.col {
    @include body-2;

    &:first-child {
        color: $neutrals4;
    }

    &:nth-child(2) {
        margin-left: auto;
        padding-left: 16px;
        font-weight: 500;
    }
}

.btns {
    margin-top: 32px;
}

.button {
    width: 100%;

    &:not(:last-child) {
        margin-bottom: 8px;
    }
}


.input {
    flex-shrink: 1;
    border-bottom: 1px solid $neutrals6;
    @include poppins;
    @include body-bold-2;
    color: $neutrals2;

    &::placeholder {
        color: $neutrals4;
    }
}
