@import "../../../styles/mixins.scss";


.section {
    padding: 96px 0;

    @include breakpoint(x) {
        padding: 80px 0;
    }

    @include breakpoint(m) {
        padding: 64px 0;
    }

    .container {
        display: flex;
        align-items: flex-start;

        @include breakpoint(t) {
            display: block;
        }
    }

    .bg {
        position: relative;
        flex-grow: 1;
        margin-right: 96px;

        @include breakpoint(x) {
            margin-right: 64px;
        }

        @include breakpoint(d) {
            margin-right: 32px;
        }

        @include breakpoint(t) {
            margin: 0 0 32px;
        }

        .preview {
            position: relative;

            img {
                width: 100%;
                border-radius: 16px;
            }

            .categories {
                position: absolute;
                top: 24px;
                left: 24px;
                display: flex;
                flex-wrap: wrap;
                margin: -8px 0 0 -8px;

                .category {
                    margin: 8px 0 0 8px;
                }
            }
        }

        .options {

        }
    }

    .details {
        display: flex;
        flex-direction: column;
        flex-shrink: 0;
        width: 384px;

        @include breakpoint(d) {
            width: 336px;
        }

        @include breakpoint(t) {
            width: 100%;
        }

        .title {
            margin-bottom: 8px;
        }

        .cost {
            display: flex;
            align-items: center;
            margin-bottom: 40px;

            .price {
                @include dm-sans;
                font-size: 16px;
                line-height: 30px;

                &:not(:last-child) {
                    margin-right: 8px;
                }
            }

            .counter {
                @include button-1;
                color: $neutrals4;
            }
        }

        .info {
            margin-bottom: 40px;
            @include body-2;
            color: $neutrals4;

            a {
                text-decoration: underline;
                font-weight: 500;
                color: $neutrals2;

                @include dark {
                    color: $neutrals8;
                }

                &:hover {
                    text-decoration: none;
                }
            }
        }

        .nav {
            display: flex;
            margin-bottom: 32px;
            padding: 6px;
            border-radius: 20px;
            box-shadow: inset 0 0 0 2px $neutrals6;

            @include dark {
                box-shadow: inset 0 0 0 2px $neutrals3;
            }

            .link {
                padding: 6px 12px;
                border-radius: 14px;
                background: none;
                @include button-2;
                color: $neutrals4;
                transition: all .2s;

                &:hover {
                    color: $neutrals3;

                    @include dark {
                        color: $neutrals6;
                    }
                }

                &.active {
                    background: $neutrals3;
                    color: $neutrals8;

                    @include dark {
                        background: $neutrals8;
                        color: $neutrals2;
                    }
                }

                &:not(:last-child) {
                    margin-right: 8px;
                }
            }
        }

        .users {
            margin-bottom: auto;
        }

        .control {
            margin-top: 40px;

            @include breakpoint(m) {
                margin-top: 32px;
            }
        }
    }
}

