@import "../../../styles/mixins.scss";

.loader,
.loaderWhite {
    width: 1em;
    height: 1em;
    border-radius: 50%;
    position: relative;
    font-size: 4px;
    animation: loader 1.1s infinite ease;
    text-indent: -9999em;
    transform: translateZ(0);
}

.loader {
    animation: loader 1.1s infinite ease;

    @include dark {
        animation-name: loader-white;
    }
}

.loaderWhite {
    animation-name: loader-white;
}

