@import "../../../../styles/mixins.scss";


.options {
    display: flex;
    padding: 8px;
    align-items: center;
    justify-content: center;
    border-radius: 32px;
    background: $neutrals8;

    @include dark {
        background: $neutrals2;
    }


    .button {
        &:not(:last-child) {
            margin-right: 24px;
        }
    }

    .favorite {
        svg {
            fill: $pink;
        }

        &:hover {
            svg {
                fill: $pink;
            }
        }
    }
}
