@import "../../../../styles/mixins.scss";

.list {
    display: flex;
    flex-wrap: wrap;
    margin: -32px -16px 0;

    @include breakpoint(x) {
        margin: -32px -10px 0;
    }

    @include breakpoint(d) {
        margin: -32px -16px 0;
    }

    @include breakpoint(m) {
        display: block;
        margin: 0;
    }
}

.card {
    flex: 0 0 calc(33.333% - 32px);
    width: calc(33.333% - 32px);
    margin: 32px 16px 0;

    @include breakpoint(x) {
        flex: 0 0 calc(33.333% - 20px);
        width: calc(33.333% - 20px);
        margin: 32px 10px 0;
    }

    @include breakpoint(d) {
        flex: 0 0 calc(50% - 32px);
        width: calc(50% - 32px);
        margin: 32px 16px 0;
    }

    @include breakpoint(m) {
        width: 100%;
        margin: 0;

        &:not(:last-child) {
            margin-bottom: 32px;
        }
    }
}

.loader {
    margin: 32px auto 10px;
}